@import "../../styles/styles.scss";
$color_bg_hero__gradient--last: #074581;
$color_bg_hero__gradient--first: $color__primary;
.home {
  .hero {
    position: relative;
    background: linear-gradient(
      90deg,
      $color_bg_hero__gradient--first 0%,
      $color_bg_hero__gradient--last 100%
    );
    &__button {
      display: flex;
      gap: rem(18);
      @include extraSmall {
        flex-direction: column;
        gap: rem(10);
      }
    }
    &__wrapper {
      display: flex;
      align-items: center;
      height: rem(800);
      @include large {
        height: rem(780);
      }
      @include medium {
        flex-direction: column;
        height: fit-content;
      }
      @include extraSmall {
      }
    }
    &__content,
    &__image {
      flex: 1 1 50%;
    }
    &__content {
      color: white;
      padding: 0 $padding__column;
      @include medium {
        margin-top: 120px;
      }
      @include small {
        margin-top: 100px;
      }
      h1,
      p {
        max-width: 100%;
      }
      h1 {
        font-size: rem(64);
        font-weight: 700;
        margin-bottom: rem(24);
        margin-top: 0;
        @include large {
          font-size: rem(56);
        }
        @include medium {
          font-size: rem(48);
        }
        @include extraSmall {
          font-size: rem(40);
        }
      }
      p {
        margin-bottom: rem(60);
        @include medium {
          margin-bottom: rem(40);
        }
      }
    }
    &__image {
      position: relative;
      align-self: flex-end;
      @include medium {
        align-self: center;
        width: 100%;
      }
      &-large {
        position: absolute;
        left: -50px;
        bottom: 0;
        z-index: 800;
        @include large {
          width: 560px;
        }
        @include medium {
          width: 460px;
          left: 50%;
          transform: translateX(-50%);
        }
        @include small {
          width: 360px;
        }
        @include extraSmall {
          width: 280px;
        }
      }
    }
    &__triangle {
      position: absolute;
    }
    &__empty-box {
      display: none;
      @include medium {
        display: block;
      }
    }
  }
  .slider {
    position: relative;
    background-color: #f0f8ff;
    z-index: 1000;
    height: rem(200);
    display: flex;
    align-items: center;
    @include medium {
      height: rem(150);
    }
    &__container {
      padding: 0 $padding__column;
    }
    &__image {
      display: flex;
      justify-content: center;
    }
  }
  .platform {
    background-color: white;
    position: relative;
    z-index: 1000;
    padding-top: rem(80);
    &__top {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 $padding__column;
      margin-bottom: 2rem;
      @include small {
        flex-direction: column;
        align-items: flex-start;
      }
      &-left,
      &-right {
        flex: 1 1 0;
      }
      &-left {
        h2 {
          margin-bottom: rem(60);
          @include small {
            margin-bottom: rem(40);
          }
        }
      }
      &-right {
        display: flex;
        justify-content: flex-end;
      }
    }
    &__title {
      font-size: rem(38);
      line-height: rem(48);
      @include medium {
        font-size: rem(24);
        line-height: rem(30);
      }
    }
    &__name {
      font-size: rem(14);
      font-weight: 500;
      color: #2d4bb6;
      letter-spacing: 1px;
      margin-bottom: rem(22);
    }
  }
  .business_platform {
    &__content {
      &-grid {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        row-gap: $row__gap--small;
        @include medium {
          grid-template-columns: repeat(2, 1fr);
        }
        @include small {
          grid-template-columns: 1fr;
        }
      }
      &-item {
        padding: 0 $padding__column;
        &-image {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 50px;
          height: 50px;
          border-radius: 50%;
          margin-bottom: rem(30);
          background-color: #ddeeff;
        }
        &-title {
          font-size: rem(20);
          font-weight: 500;
          margin-bottom: rem(24);
        }
      }
    }
    &__image {
      grid-column: 1/5;
      position: relative;
      align-self: center;
      text-align: center;
      z-index: 900;
      @include medium {
        grid-column: 1/3;
      }
      @include small {
        grid-column: auto;
      }
      img {
        width: 80%;
      }
    }
  }
  .platform__name--green {
    color: #059459;
  }
  .platform__title--white {
    color: white;
  }
  .background--dark-blue {
    background-color: #031a30;
  }
  .personal_platform {
    &__content {
      &-item {
        padding: 0 $padding__column;
        &-image {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 50px;
          height: 50px;
          border-radius: 50%;
          margin-bottom: rem(30);
          background-color: #befde3;
        }
        &-title {
          font-size: rem(20);
          font-weight: 500;
          margin-bottom: rem(24);
        }
        &-description {
          color: #cdd1d6;
        }
      }
    }
    &__image {
      position: relative;
      grid-column: 2/5;
      text-align: center;
      z-index: 900;
      @include medium {
        grid-column: 1/3;
      }
      @include small {
        grid-column: auto;
      }
      img {
        width: 80%;
        @include medium {
          width: rem(400);
        }
        @include small {
          width: rem(300);
          bottom: rem(-400);
        }
      }
    }
  }
  .simplify {
    background-color: $color__primary;
    color: white;
    padding-top: rem(80);
    position: relative;
    &__container {
      padding-bottom: rem(240);
      @include medium {
        padding-bottom: rem(120);
      }
      @include small {
        padding-bottom: rem(100);
      }
    }
    &__why {
      padding: 0 $padding__column;
    }
    &__title {
      font-size: rem(38);
      font-weight: 700;
      margin-bottom: rem(40);
      padding: 0 $padding__column;
      margin-top: rem(24);
    }
    &_flexbox {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: rem(80);
      @include medium {
        gap: rem(20);
        flex-direction: column;
      }
      &__content,
      &__image {
        flex: 1 1 0;
      }
      &__image {
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        img {
          width: 100%;
        }
      }
      &__content {
        padding: 0 $padding__column;
        &-title {
          font-size: rem(28);
          font-weight: 700;
          margin-bottom: rem(20);
        }
        p {
          margin-bottom: rem(20);
          color: #cdd1d6;
          line-height: rem(25);
        }
      }
    }
  }
}

.image__background {
  position: absolute;
  right: 0;
  bottom: -60px;
  width: calc(50vw + 720px);
  max-height: 300px;
  max-width: calc(50vw + 550px);
  @include extraLarge {
    width: 85%;
  }
  @include larger {
    width: 97%;
    bottom: -30px;
  }
  @include small {
    bottom: -20px;
  }
  &-personal {
    position: absolute;
    right: 0;
    bottom: -60px;
    width: calc(50vw + 360px);
    max-height: 300px;
    max-width: calc(50vw + 550px);
    @include extraLarge {
      width: 70%;
    }
    @include larger {
      width: 75%;
      bottom: -30px;
    }
    @include medium {
      width: 95%;
    }
  }
}
